import {
	getRiseInfo,
	copyRiseInfo
} from '@/service/service.js';
export default {
	data() {
		return {
			rise: [],
			navigateToNextDelta: false
		}
	},
	onLoad(option) {
		this.showRiseInfo(); //获取抬头
		console.log(3121,option)
		this.navigateToNextDelta = option.navigateToNextDelta;
	},
	onShow() {
		// this.showRiseInfo(); //获取抬头
		
	},
	methods: {
		// 进入抬头详情
		handleRiseDetail(idx) {
			console.log(idx)
			let rise_data = this.rise[idx];
			rise_data = JSON.stringify(rise_data);
			console.log(this.rise[idx])
			uni.navigateTo({
				url: './invoice-detail_tt?rise_data=' + rise_data
			});
		},			
		//复制到剪切板
		copyRiseInfo(index) {
			console.log(index)
			let info = this.rise[index];
			let str = info.title + '\n' + info.companyTaxNo;
			uni.setClipboardData({
			    data: str,
			    success: function () {
			        // console.log('success');
					uni.showToast({
						title: "已复制到剪切板",
						icon: 'none',
						duration: 2000
					});
			    }
			});
		},
		//获取抬头
		showRiseInfo() {
			getRiseInfo().then((res) => {
				if (res.statusCode === 1) {
					var flag = false;
					res.data.map((ev) => {
						if(flag) {
							ev.defaulted = false;
						}
						this.rise.push(ev)
						if(ev.defaulted) {
							flag = true;
						}
					})
				}
			})
		},
		// 新增抬头
		handleAddInvoice() {
			uni.navigateTo({
				url: './invoice-add_tt'
			})
		}
	}
}
